.aboutbanner-section {
    position     : relative;
    background   : linear-gradient(180deg, #FFFFFF -6.18%, #FFF4F4 101.72%);
    border-radius: 12px;

    @media (min-width: 767px) {
        padding      : 42px 100px 320px;
        position     : relative;
        margin       : 0px 15px 250px 15px;
        border-radius: 12px;
    }

    .banner-img {
        position: relative;

        @media (min-width: 767px) {
            position: absolute;
            width: 100%;
            left: 0px;
        }
    }
}

.brand-section {
    padding: 78px 0px 77px;

    @media (min-width: 767px) {
        position: relative;
        padding : 150px 0 75px;
    }

    #nav-tab {
        .nav-item {
            padding       : 0px 0px 19px 0px;
            line-height   : 32px !important;
            letter-spacing: -0.5px;
            font-weight   : 500;
            color         : #1D1919;
            opacity       : 0.7;
            background    : transparent;
            border        : none;
            cursor        : pointer;

            @media(max-width: 767px) {
                // flex: none;
                font-size: 16px !important;
                padding  : 16px 5px;
            }
        }

        .nav-item.active {
            padding       : 0px 0px 16px 0px;
            line-height   : 32px !important;
            letter-spacing: -0.5px;
            font-weight   : 800;
            color         : #1B4A52;
            opacity       : 1;
            background    : transparent;
            border-bottom : 3px solid #1B4A52;

            @media(max-width: 767px) {
                font-size: 16px !important;
                padding  : 16px 5px;
            }
        }
    }

    .brand-slider-tabpane {
        margin-top: 80px;

        @media(max-width: 767px) {
            margin-top: 41px;
        }

        .js-partners-icons,.js-partners-icons-reverse,.js-pharma-icons,.js-pharma-icons-reverse{
            overflow: hidden;
        }

        .logo-size {
            width        : 278px;
            height       : 168px;
            text-align   : center;
            border       : 1px solid #B1CBCF;
            border-radius: 24px;
            // padding      : 64px 10px;
            display      : inline-flex;            
            cursor:pointer;         

            @media(max-width: 767px) {
                width        : 100%;
                height       : 65px;
                padding      : 4px 4px;
                border-radius: 12px;
            }

        }
    }
}




.mission-section {
    position: relative;

    @media (min-width: 767px) {
        padding : 146px 100px 146px;
        position: relative;
    }


    .mission-list {
        position: relative;

        @media (max-width: 766px) {
            display       : flex;
            flex-direction: column-reverse;
        }

        .mission-div {   
            padding-right: 30px; 
            @media (max-width: 766px) {
                display       : flex;
                flex-direction: column-reverse;
                padding-right: 15px;
            }
            .mission-text {
                padding-right: 115px;

                @media (max-width: 767px) {
                    padding-right: 0px;
                    margin-top   : 40px;
                }
            }

            .mission-img {
                margin-top: 48px;
            }
        }

        .vision-div {
            padding-left: 30px;
            @media (max-width: 766px) {
                padding-left: 15px;
                margin-top: 65px;
            }
            .vision-text {
                margin-top   : 48px;
                padding-right: 115px;

                @media (max-width: 767px) {
                    padding-right: 0px;
                    margin-top   : 40px;
                }
            }
        }

    }
}


.builder-section {
    background   : linear-gradient(180deg, #FFFFFF -17.46%, #FEFAF6 47.11%);
    margin       : 0px 15px;
    border-radius: 12px;
    position     : relative;
    padding      : 88px 0px 37px;

    @media(min-width: 767px) {
        padding: 92px 100px 205px;
    }

    .builder-row {
        @media(max-width: 767px) {
            display       : flex;
            flex-direction: column-reverse;
            ;
        }

        // builder-swiper
        .swiper {
            width       : 100%;
            max-height  : 455px;
            padding-left: 45px;
            margin-top: 72px;

            @media(max-width: 767px) {
                max-height  : 530px;
                padding-left: 25px;
            }
        }

        .swiper-slide {
            display: flex;

            .builder-card {              

                @media(max-width: 767px) {
                    padding-right: 25px;
                }

                .builder-img {
                    margin-bottom: 27px;
                }
            }
        }

        .swiper-pagination-bullets {
            right : inherit;
            left  : 0px;
            top   : 50% !important;
            height: 65px;

            @media(max-width: 767px) {
                transform: rotate(-90deg) !important;
                left     : 150px;
                top      : 90% !important;
                height   : 58px;
                width    : 40px;
                z-index  : 9;
            }

            .swiper-pagination-bullet {
                // display: none !important;
                margin: 10px 0px !important;

                @media(max-width: 767px) {
                    height       : 20px;
                    width        : 3px;
                    border-radius: 4px;
                }
            }

            .swiper-pagination-bullet-active {
                background: #1B4A52 !important;
            }
        }


        .builder-right {
            position: relative;

            .builder-right-card {
                width          : 85%;
                padding        : 48px 48px;
                height         : 279px;
                background     : rgba(255, 255, 255, 0.4);
                position       : absolute;
                transform      : translateY(390px);
                inset          : 0;
                z-index        : 2;
                backdrop-filter: blur(10px);
                border-radius  : 24px;
                border         : 1px solid rgba(255, 255, 255, 0.2);
                left           : inherit;
                right: -40px;

                @media (max-width: 767px) {
                    width    : 95%;
                    position : relative;
                    left     : 0px;
                    margin   : 0px auto;
                    transform: translateY(-50px);
                    padding  : 24px 20px;
                }
            }

            h2 {
                width: 60%;

                @media (max-width: 767px) {
                    width: 70%;
                }
            }
        }
    }
}



.team-section {
