}


.opportunity-section {
    padding: 80px 0px 84px;

    @media(min-width: 768px) {
        padding: 130px 0px 130px;
    }

    .opportunity__head {
        margin-bottom: 48px;
    }

    .opportunity-mobile {
        @media(max-width: 767px) {
            position     : relative;
            height       : auto;
            // overflow-x: scroll;
            overflow-y   : hidden;
            top          : 0%;
        }

        .options {
            display       : flex;
            flex-direction: row;
            align-items   : stretch;
            overflow      : hidden;
            min-width     : 700px;
            max-width     : 100%;
            width         : calc(100% - 0px);
            height        : 500px;
            position      : relative;
            z-index       : 1;
            top           : 0;
            left          : 0%;

            @media(max-width: 767px) {
                height   : 100%;
                left     : 0px;
                min-width: 955px;
            }

            .option {
                position           : relative;
                width              : 25%;
                overflow           : hidden;
                background-position: center;
                cursor             : pointer;
                transition         : .5s cubic-bezier(0.05, 0.61, 0.41, 0.95);

                @media(max-width: 767px) {
                    margin-right: 16px;
                    width       : 100%;
                }

                .option-card {
                    height      : 100%;
                    margin-right: 10px;

                    .option__img {
                        height  : 100%;
                        width   : 100%;
                        float   : right;
                        position: relative;
                        object-fit: cover;
                        border-radius: 20px;

                        @media(max-width: 767px) {
                            height: 245;
                            top   : 0%;
                        }
                    }
                }

                &.active {
                    flex-grow      : 10000;
                    transform      : scale(1);
                    max-width      : 700px;
                    margin         : 0px;
                    background-size: auto 100%;

                    @media(max-width: 767px) {
                        margin-right: 16px;
                    }
                }

                &:not(.active) {
                    flex-grow: 1;
                }
            }
        }
    }

    .scrollbar {
        float     : left;
        background: transparent;
        overflow-y: scroll;
    }

    #style-3::-webkit-scrollbar-track {
        // -webkit-box-shadow: inset 0 0 0px rgba(0, 0, 0, 0.0);
        background-color  : transparent;
    }

    #style-3::-webkit-scrollbar {
        width           : 1px;
        background-color: transparent;
    }

    #style-3::-webkit-scrollbar-thumb {
        background-color: transparent;
    }


    .opportunity-right {
        @media(max-width: 767px) {
            margin-top: 56px;
        }
    }
}





.investor-section {
    background   : linear-gradient(180deg, #FFFFFF 0%, #FEFAF6 33.02%);
    margin       : 0px 15px 15px 15px;
    border-radius: 12px;
    padding      : 56px 0px 113px;

    @media(min-width: 767px) {
        padding: 56px 0px 92px;
    }
    .investor-icons {
        margin-top    : 84px;
        padding-bottom: 55px;
        position: relative;
        overflow: hidden;       

        @media(max-width: 767px) {
            margin-top: 36px;
        }
        &::after {
            position  : absolute;
            content   : "";
            top       : -105%;
            width     : 240px;
            height    : 300px;
            z-index   : 9;
            left      : -20px;
            background: linear-gradient(180deg, #FFFBF7 22.43%, rgba(255, 255, 255, 0) 91.36%);
            transform: rotate(-90deg);      
            @media(max-width: 767px) {
                content: none;
            }
        }
        &::before {
            position  : absolute;
            content   : "";
            top       : -105%;
            width     : 240px;
            height    : 300px;
            z-index   : 9;
            right     : -20px;
            background: linear-gradient(180deg, #FFFBF7 22.43%, rgba(255, 255, 255, 0) 91.36%);
            transform: matrix(0, -1, -1, 0, 0, 0);    
            @media(max-width: 767px) {
                content: none;
            }
        }
        .investor-logo {
            width     : 278px;
            height    : 115px;
            text-align: center;
            padding   : 42px 10px;
            text-align: center;
            display: inline-flex;           

            @media(max-width: 767px) {
                width        : 100%;
                height       : 65px;
                padding      : 10px 10px;
                border-radius: 12px;
            }
        }
        .swiper-pagination-bullet {
            width        : 15px !important;
            border-radius: 3px !important;
            height       : 5px;
        }
        .swiper-pagination-bullet-active {
            background: #000 !important;
        }
    }
}




