} */
@keyframes FadeInUp {
	0% {
		transform: translateX(105px);
		opacity: 0;
	}
	100% {
		transform: translateX(0px);
		opacity: 1;
	}
}
.process-wrapper {
	margin: auto;
	max-width: 1080px;
}
#progress-bar-container {
	position: relative;
	width: 100%;
	margin: auto;
	height: 100px;
	margin-top: 65px;
	ul {
		padding: 0;
		margin: 0;
		padding-top: 15px;
		z-index: 9999;
		position: absolute;
		width: 100%;
		margin-top: -40px;
	}
	li.active {
        .logo{
            stroke: #FCFCFC;
        }
		&:before {
			background: #E2504B;
		}
		&:after {
			background: #E2504B;
            .logo{
                fill: #FCFCFC;
            }
		}
	}
	li {
        svg{
            right: 98px;
            position: absolute;
            top: 71px;
        @media (max-width: 766px) {
            right: 38px;
        }
        }
        .logo{
            stroke: #E2504B;;
        }
        
		&:hover {
			&:before {
				background: #E2504B;
			}
		}
		list-style: none;
		float: left;
		width: 20%;
		text-align: center;
		color: #aaa;
		text-transform: uppercase;
		font-size: 11px;
		cursor: pointer;
		font-weight: 700;
		transition: all ease 0.2s;
		vertical-align: bottom;
		// height: 60px;
		position: relative;
		.step-inner {
			position: absolute;
			width: 100%;
			bottom: -55px;
            font-weight: 800;
            font-size: 20px;
            line-height: 27px;
            color: #0E0E0E;
		}
		&:after {
			content: " ";
			display: block;
			width: 60px;
			height: 60px;
			background: #f6dcda;
			margin: auto;
			border-radius: 50%;
			margin-top: 53px;
			transition: all ease 0.2s;
		}
	}
	#line {
		width: 82%;
		margin: auto;
		background: #eee;
		height: 6px;
		position: absolute;
		left: 10%;
		top: 57px;
		z-index: 1;
		border-radius: 50px;
		transition: all ease 0.9s;
	}
	#line-progress {
		content: " ";
		width: 3%;
		height: 100%;
		background: #E2504B;
		position: absolute;
		z-index: 2;
		border-radius: 50px;
		transition: all ease 0.9s;
	}
}
#progress-bar-container1 {
	position: relative;
	width: 90%;
	margin: auto;
	// height: 100px;
	ul {
		padding: 0;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;
		margin: 0;
		z-index: 9999;
		position: absolute;
		width: 100%;
		margin-top: -40px;
	}
	li {
		list-style: none;
		width: 3%;
		text-align: center;
		color: #aaa;
		text-transform: uppercase;
		font-size: 11px;
		cursor: pointer;
		font-weight: 700;
		transition: all ease 0.2s;
		vertical-align: bottom;
		// height: 60px;
		position: relative;

        @media (max-width: 767px) {
            margin-right: 15px;
        }
		&:after {
			content: " ";
			display: block;
			width: 20px;
			height: 6px;
			background: #d0d5d6;
			margin: auto;
			border-radius: 200px;
			transition: all ease 0.2s;
		}
	}
	li.active {
		&:after {
			background: #1B4A52;
			border-radius: 200px;
		}
	}
}
#progress-content-section {
	width: 90%;
    margin-top: 60px;
	margin: auto;
	border-radius: 4px;
	.section-content {
		// padding: 0px 40px;
		text-align: center;
		margin-top: 100px;
		display: none;
		animation: FadeInUp 700ms ease 1;
		animation-fill-mode: forwards;
		transform: translateX(105px);
		opacity: 0;
		h2 {
			font-weight: 500;
            font-size: 32px;
            line-height: 40px;
            letter-spacing: 1px;
            color: #1D1919;
            text-align: left;
            width: 344px;
            height: 81px;
		}
		p {
			color: #777;
            font-weight: 500;
            font-size: 16px;
            line-height: 28px;
            opacity: 0.7;
            text-align: justify;
            width: 444px;
            height: 105px;
		}
	}
	.section-content.active {
		display: flex;
        min-height: 400px;
        @media (max-width: 766px) {
            display: block;
        }
	}
}
.swiper {
	width: 100%;
	height: 100%;
}
.swiper-slide {
	text-align: center;
	font-size: 18px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	-webkit-justify-content: center;
	justify-content: center;
	-webkit-box-align: center;
	-ms-flex-align: center;
	-webkit-align-items: center;
	align-items: center;

.slide-img {
    width: 40%;
    margin-right: 110px;

    @media (max-width: 766px) {
        width: 100%;
        margin-right: 0px;
    }
        
    img {
        border-radius: 24px;
    }
}
        
.slide-content {
    width: 50%;
    padding: 110px 0px;
        
    @media (max-width: 766px) {
        width: 100%;
        padding: 80px 0;
    }
    .slide-icon {
        width: 10%;
        margin-bottom: 31px;
    }
}
}
.swiper-pagination-bullet {
	width: 20px;
	height: 20px;
	text-align: center;
	line-height: 20px;
	font-size: 12px;
	color: #000;
	opacity: 1;
	background: rgba(0, 0, 0, 0.2);
